import { Component, EventEmitter, Inject, OnInit, Output } from '@angular/core';
import { IMovimientoConversion } from '../../models/movimiento-conversion';
import { MovimientoConversionService } from '../../service/movimiento-conversion.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { AppAlertService } from 'app/shared/service/app/alert.service';
import { Observable, Subject, finalize, firstValueFrom, map, startWith, takeUntil, takeWhile, tap, timer } from 'rxjs';
import { MAT_DIALOG_DATA, MatDialog, MatDialogRef } from '@angular/material/dialog';
import { MAT_DATE_FORMATS } from '@angular/material/core';
import { MatTableDataSource } from '@angular/material/table';
import { DatePipe } from '@angular/common';
import { MY_DATE_FORMATS } from '@shared/constants/date-format';
import { IArticulo } from 'app/modules/stock/articulos/models/articulo';
import { IDeposito } from 'app/modules/stock/depositos/models/deposito';
import { IAlertConfig } from '@shared/models/alert';

@Component({
    selector: 'app-save-movimiento-conversion',
    templateUrl: './save.component.html',
    styleUrls: ['./save.component.scss'],
    providers: [{ provide: MAT_DATE_FORMATS, useValue: MY_DATE_FORMATS }]
})
export class SaveComponent implements OnInit {
    form: FormGroup;
    movimiento_conversion: IMovimientoConversion;
    title: string;
    user: any;
    depositos: IDeposito[] = [];
    articulos: any[] = [];
    unidades_de_medida_en_stock: any[] = [];
    all_unidades_de_medida: any[] = [];
    filteredDepositos: Observable<IDeposito[]>;
    filteredArticulos: Observable<IArticulo[]>;
    articulos_list = [];
    unidades_de_medida_destino: any[] = [];
    maxPalabras: number = 50;
    minDate: Date;
    maxDate: Date;
    showAlert = false;
    _alertConfig: IAlertConfig;
    private _unsubscribeAll: Subject<any> = new Subject<any>();
    @Output() public creadoExitosamente: EventEmitter<void> = new EventEmitter<void>();

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private _movimientoConversionService: MovimientoConversionService,
        private datePipe: DatePipe,
        public alert: AppAlertService,
        public dialogRef: MatDialogRef<SaveComponent>,
    ) {
        this.movimiento_conversion = this.data ? this.data : null;
        this.title = this.movimiento_conversion?.idmovimientoconversion ? `Editar conversion ${this.movimiento_conversion.fecha} ${this.movimiento_conversion.hora}` : `Nueva conversion`;
        this.user = JSON.parse(localStorage.getItem('user'));
        // Se define minDate como null o cualquier otra fecha límite que desees permitir hacia atras
        this.minDate = null;
        // Define maxDate como el dia de hoy
        this.maxDate = new Date();
    }

    ngOnInit(): void {
        this.exist();
        this.newForm();
        this.subscribeAlert();
    }

    // Alert
    subscribeAlert() {
        this.alert.emitConfig.subscribe({
            next: (config: IAlertConfig) => {
                this.showAlert = true;
                this._alertConfig = {
                    ...config,
                    appearance: 'border',
                };
                let time = 2;
                timer(100, 1500)
                    .pipe(
                        finalize(() => {
                            this.showAlert = false;
                            this._alertConfig = null;
                        }),
                        takeWhile(() => time > 0),
                        takeUntil(this._unsubscribeAll),
                        tap(() => (time -= 1))
                    )
                    .subscribe();
            },
        });
    }

    newForm() {
        const [dia, mes, anio] = this.movimiento_conversion ? this.movimiento_conversion.fecha.split('/') : ''; //Se hace esto para poder manejar el formato que se cambio en el render del componente de tabla dinamica.

        this.form = new FormGroup({
            fecha: new FormControl((this.movimiento_conversion?.fecha ? this.datePipe.transform(new Date(`${mes}/${dia}/${anio}`), 'yyyy-MM-dd') : this.datePipe.transform(new Date(), 'yyyy-MM-dd')), [Validators.required]),
            hora: new FormControl((this.movimiento_conversion?.hora ? this.movimiento_conversion.hora : this.datePipe.transform(new Date(), 'HH:mm')), [Validators.required]),
            deposito: new FormControl((this.movimiento_conversion ? { iddeposito: this.movimiento_conversion.iddeposito, descripcion: this.movimiento_conversion.deposito } : ''), Validators.required),
            articulo: new FormControl((this.movimiento_conversion ? {
                codigo_interno: this.movimiento_conversion.codigo_interno_articulo,
                idarticulo: this.movimiento_conversion.idarticulo,
                descripcion: this.movimiento_conversion.articulo
            } : null), Validators.required),
            unidad_medida_origen: new FormControl((this.movimiento_conversion ? this.movimiento_conversion.unidad_medida_origen : ''), Validators.required),
            unidad_medida_destino: new FormControl((this.movimiento_conversion ? this.movimiento_conversion.unidad_medida_destino : ''), Validators.required),
            cantidad: new FormControl((this.movimiento_conversion ? this.movimiento_conversion.cantidad : ''), Validators.required),
            observaciones: new FormControl((this.movimiento_conversion ? this.movimiento_conversion.observaciones : ''), [Validators.maxLength(this.maxPalabras)]),
            stock_existente: new FormControl(('')),
        });

        this.form.get('unidad_medida_destino').disable();

        if (this.movimiento_conversion) {
            this.form.get('unidad_medida_origen').disable();
            this.form.get('fecha').disable();
        }
    }

    /**
    * Funcion para contar las palabras en las observaciones
    */
    getPalabrasLength(): number {
        const observaciones = this.form.get('observaciones').value;
        return observaciones ? observaciones.length : 0;
    }

    /**
    * Funcion para verificar si se ha alcanzado el límite de caracteres en las observaciones
    */
    isPalabraMaxLengthReached(): boolean {
        return this.getPalabrasLength() <= this.maxPalabras;
    }

    /**
    *Esta funcion se encarga de manejar las acciones necesarias segun sí el formulario es para crear o editar.
    */
    exist() {
        if (this.movimiento_conversion?.idmovimientoconversion) {
            this.getDepositos(true);    //Cargó todos los depósitos...
            this.getArticulosDepositosStock(this.movimiento_conversion.iddeposito, this.movimiento_conversion.idarticulo, this.movimiento_conversion.unidad_medida_origen) //Solicito los articulos que hay en stock en un depósito en concreto...
                .then(() => {   //...al final la peticion y asegurarme que esten todos los datos necesarios...
                    this.getUnidadesDeMedidaArticulosStock(this.movimiento_conversion.idarticulo) //...hacemos un filtrado de las unidades de medida de los articulos que estan en stock.
                })

        } else {
            this.getDepositos();
        }
    }

    /**
     * Esta funcion lista los depósitos.
     * @param option 
     */
    async getDepositos(option?) {
        const Depositos = await firstValueFrom(this._movimientoConversionService.getDepositos(option));
        new Promise(async (resolve) => {
            this.depositos = Depositos.list;
            this.filteredDepositos = this.form.get('deposito').valueChanges.pipe(
                startWith(''),
                map((value) => this._filterDepositos(value)),
            );

            resolve(true);
        })
    };

    /**
     * Filtro que ayuda en el autocompletado del input correspondiente.
     * @param deposito 
     * @returns 
     */
    private _filterDepositos(deposito: IDeposito): IDeposito[] {
        const filterValue = deposito.toString().toLowerCase();
        return this.depositos.filter(deposito => deposito.descripcion.toLowerCase().includes(filterValue));
    }

    /**
     * Funcion que se encarga de obtener el valor textual para mostrarlo en el input de autocompletado.
     * @param option 
     * @returns 
     */
    getTextDeposito(option) {
        if (option.descripcion) {
            return option.descripcion;
        }
        if (option.deposito) {
            return option.deposito;
        }
        return option;
    }

    /**
    * Funcion que lista los articulos que posee un déposito.
    */
    async getArticulosDepositosStock(iddeposito, idarticulo?, idunidadmedida?) {
        const articulos_um_deposito = await firstValueFrom(this._movimientoConversionService.getArticulosDepositosStock(iddeposito, idarticulo, idunidadmedida)); //solicitamos los aticulos que esten en un deposito
        new Promise(async (resolve) => {
            this.articulos = articulos_um_deposito.list;
            if (this.movimiento_conversion) {
                let stock = articulos_um_deposito.list[0].stock;
                this.form.get('stock_existente').setValue(Number(stock));
            }
            this.filteredArticulos = this.form.get('articulo').valueChanges.pipe(
                startWith(''),
                map((value) => this._filterArticulos(value)),
            );
            resolve(true);
        });

        //Esta funcion la realizamos para listar los articulos sin que se repitan.
        articulos_um_deposito.list.forEach(item => {
            const existeArticulo = this.articulos_list.find(articulo => articulo.idarticulo === item.idarticulo);
            if (!existeArticulo) {
                let articulo = { idarticulo: item.idarticulo, codigo_interno: item.codigo_interno, descripcion: item.descripcion };
                this.articulos_list.push(articulo);
            };
        });
    }

    /**
* Esta funcion maneja las cambios de seleccion que puedan tener articulos y unidades de medida.
* @param $event 
*/
    seleccionArticulo($event) {
        //Al seleccionar un articulo...
        if ($event.idarticulo) {
            this.getUnidadesDeMedidaArticulosStock($event.idarticulo) //...Buscamos las UM que posee ese articulo.
            //Y habilitamos los demas inputs
            this.form.get('unidad_medida_origen').enable()
            this.form.get('cantidad').enable()
        }

        //Al seleccionar una UM...
        if ($event.um) {
            this.unidades_de_medida_destino = this.all_unidades_de_medida.filter(x => x.idunidadmedida != $event.um); //Filtramos las unidades de media distintas a la seleccionada en el origen
            let articulo_um = this.unidades_de_medida_en_stock.find(a => a.idarticulo === this.form.get('articulo').value.idarticulo && a.idunidadmedida === this.form.get('unidad_medida_origen').value); //Consultamos sí existe un articulo con esa UM.
            let stock = articulo_um.stock; //variable auxiliar  
            this.form.get('stock_existente').setValue(stock);
            this.form.get('unidad_medida_destino').enable();
        }

        //Al seleccionar un depósito...
        if ($event.iddeposito) {
            this.getArticulosDepositosStock($event.iddeposito);
        }
    }

    /**
     * Esta funcion es para filtrar los artículos según el input que se haya ingresado en el buscador de articulos.
     * @param value 
     * @returns 
     */
    private _filterArticulos(value: string): IArticulo[] {
        const filterValue = value.toString().toLowerCase();
        return this.articulos_list.filter(articulo => {
            const codigo_interno = articulo.codigo_interno.toString().toLowerCase().includes(filterValue);
            const descripcion = articulo.descripcion.toString().toLowerCase().includes(filterValue);
            return codigo_interno || descripcion;
        });
    }

    /**
     * Obtenemos la descripcion del articulo seleccionado.
     * @param articulo 
     * @returns 
     */
    getTextArticulo(articulo: IArticulo): string {
        return articulo ? `${articulo.codigo_interno} - ${articulo.descripcion}` : '';
    }

    /**
     * Esta funcion solicita las unidades de medida que posee un articulo en stock en el depósito.
     * @param idarticulo 
     */
    async getUnidadesDeMedidaArticulosStock(idarticulo?) {
        await new Promise(async (resolve) => {
            this.unidades_de_medida_en_stock = this.articulos.filter(a => a.idarticulo === idarticulo).map(a => { return { idarticulo: a.idarticulo, idunidadmedida: a.idunidadmedida, descripcion_um: a.um, stock: a.stock } }); //Filtramos para obtener las unidades de medida solamente de los articulos de ese deposito.
            this.getUnidadesDeMedidaArticulo(idarticulo)
            resolve(true);
        })
    }

    /**
     * Esta funcion se encarga de llamar a todas las UM que tenga un articulo sin importar si existe o no  en el deposito.
     * @param idarticulo 
     */
    async getUnidadesDeMedidaArticulo(idarticulo) {
        new Promise(async (resolve) => {
            const unidadesmedidas = await firstValueFrom(this._movimientoConversionService.getUnidadesDeMedidaArticulos(idarticulo));
            this.all_unidades_de_medida = unidadesmedidas.list.filter(a => a.idarticulo === idarticulo).map(a => { return { idarticulo: a.idarticulo, idunidadmedida: a.idunidadmedida, descripcion_um: a.um.descripcion } });
            this.unidades_de_medida_destino = unidadesmedidas.list.filter(a => a.idarticulo === idarticulo).map(a => { return { idarticulo: a.idarticulo, idunidadmedida: a.idunidadmedida, descripcion_um: a.um.descripcion } });;

            if (this.movimiento_conversion?.idmovimientoconversion) {//En caso de edicion...
                this.unidades_de_medida_destino = this.unidades_de_medida_destino.filter(umd => umd.idunidadmedida != this.movimiento_conversion.unidad_medida_origen); //...Listar solo las UM distintas a la UM de origen.
            }

            resolve(true)
        })
    }

    /**
    * Funcion para limpiar campos
    */
    limpiarCasillero($event) {
        if ($event == 'articulo') {
            this.form.get('articulo').setValue('');
            this.form.get('unidad_medida_origen').disable();
            this.form.get('unidad_medida_origen').setValue('');
            this.form.get('unidad_medida_destino').disable();
            this.form.get('unidad_medida_destino').setValue('');
            this.form.get('stock_existente').setValue('');
            this.form.get('cantidad').setValue('');
            this.form.get('cantidad').disable();
        }

        if ($event == 'unidad_medida_origen') {
            this.form.get('unidad_medida_origen').setValue('');
            this.form.get('stock_existente').setValue('');
            this.form.get('cantidad').setValue('');
            this.form.get('unidad_medida_destino').setValue('');
            this.form.get('unidad_medida_destino').disable();
        }

        if ($event == 'unidad_medida_destino') {
            this.form.get('unidad_medida_destino').setValue('');
        }

        if ($event == 'cantidad') {
            this.form.get($event).setValue('');
        }

        if ($event == 'deposito') {
            this.form.get('deposito').setValue('');
            this.articulos_list = [];
        }
    }

    /**
     * Esta funcion es la encargada de encapsular y enviar los datos al servicio.
     * @returns 
     */
    onSubmit() {
        // Verificar la longitud de las observaciones
        if (!this.isPalabraMaxLengthReached()) {
            return this.alert.error(`La longitud máxima de observaciones es de ${this.maxPalabras} caracteres.`);
        }

        if (!this.form.valid) {
            return this.alert.error('Revise Los Datos Ingresados');
        } else {
            if (this.form.get('cantidad').value > this.form.get('stock_existente').value) {
                this.form.get('cantidad').setValue('');
                return this.alert.warning('La cantidad a convertir no puede ser mayor al Stock Existente!');
            }

            if (this.form.get('cantidad').value < 1) {
                this.form.get('cantidad').setValue('');
                return this.alert.warning('La cantidad a convertir no puede ser un valor negativo!');
            }

            //El 'Payload' es lo que espera el backend para poder persistir en la BD.
            const payload = {
                idusuario: this.user.id,
                fecha_hora: `${this.form.get('fecha').value} ${this.form.get('hora').value}`,
                iddeposito: this.form.get('deposito').value.iddeposito,
                idarticulo: this.form.get('articulo').value.idarticulo,
                unidad_medida_origen: this.form.get('unidad_medida_origen').value,
                unidad_medida_destino: this.form.get('unidad_medida_destino').value,
                cantidad: this.form.get('cantidad').value,
                observaciones: this.form.get('observaciones').value,
            }

            this._movimientoConversionService.saveMovimientoConversion(payload)
                .subscribe({
                    next: (data) => {
                        if (data) {
                            this.creadoExitosamente.emit();
                            this.dialogRef.close(data);
                        }
                    }
                });
        }
    }

    onCloseMenu() {
        this.dialogRef.close(false)
    }
}