import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core';
import { IMovimientoConversion } from '../models/movimiento-conversion';
import { MovimientoConversionService } from '../service/movimiento-conversion.service';
import { Subscription, debounceTime, distinctUntilChanged, firstValueFrom } from 'rxjs';
import { MatTableDataSource } from '@angular/material/table'
import { SaveComponent } from './save/save.component';
import { FuseConfirmationService } from '@fuse/services/confirmation';
import { AppAlertService } from '@shared/service/app/alert.service';
import { FormControl } from '@angular/forms';
import { TablaDinamicaComponent } from '@shared/components/tabla-dinamica/tabla-dinamica.component';
import { MatDialog } from '@angular/material/dialog';
import { environment } from 'environments/environment';
import { SearchService } from 'app/layout/common/search/search.service';
import { MatButton } from '@angular/material/button';
import { JedsMenuService } from '@shared/service/app/jeds-menu.service';
import { FilterMenuComponent } from './filter-menu/filter-menu.component';
import { IFilter } from '@shared/models/filter';
import { HeaderTextService } from '@shared/components/header-text/header-text.service';
import { Router } from '@angular/router';
@Component({
    selector: 'app-movimiento-conversion',
    templateUrl: './movimiento-conversion.component.html',
    styleUrls: ['./movimiento-conversion.component.scss']
})
export class MovimientoConversionComponent implements OnInit, OnDestroy {
    movimientos_conversion: IMovimientoConversion[];
    public dataSource: MatTableDataSource<IMovimientoConversion> = new MatTableDataSource();
    headers = ['Fecha', 'Hora','Responsable', 'Depósito','Artículo','Cantidad' , 'UM Origen', 'Cantidad','UM Destino', 'Acciones'];
    column_params = ['fecha','hora', 'usuario','deposito', 'articulo','cantidad' , 'descripcion_um_origen','cantidad_conversion','descripcion_um_destino', 'acciones'];
    funcionesObjeto = null;
    filtroBusqueda: string = '';
    filtersLike = ['observaciones', 'usuario', 'deposito'];
    filter: IFilter = {
        deposito: -1,
        filtroFechaInicio: '',
        filtroFechaFin: ''
    };
    @Input() extraParams = '';
    @Output() parametrosActualizados = new EventEmitter<any>();
    env = environment.stock.movimiento_conversion + '?sort=-fecha';
    search: FormControl = new FormControl('');
    sortValue: string;
    FilterEventSub$: Subscription;
    SearchTextSub$: Subscription;


    constructor(
        private _movimientoConversionService: MovimientoConversionService,
        private confirm: FuseConfirmationService,
        private alert: AppAlertService,
        private dialogo: MatDialog,
        private _searchService: SearchService,
        private _menuService: JedsMenuService,
        private router: Router,
        public _headerTextService: HeaderTextService,

    ) {
        this._headerTextService.setTitulo(this.router);

        /**
         * Aca se declaran los botones que iran en la grilla sobre el apartado de acciones.
         */
        this.funcionesObjeto = [{
            icono: 'remove_red_eye',
            nombre_boton: "Ver",
            functionName: 'ver',
            //functionParams: 'id' // vacio o parametros, se manda desde el hijo el $event.data.[param]
        },
        {
            icono: 'delete',
            nombre_boton: "Eliminar",
            functionName: 'eliminar',
        }
        ];
    }

    @ViewChild(TablaDinamicaComponent) tabla: TablaDinamicaComponent;
    ngOnInit(): void {
        this.SearchTextSub$ = this._searchService.getSearchText().pipe( //Se obtienen los valores que coinciden con lo ingresado en la barra de busqueda
            debounceTime(750),
            distinctUntilChanged()
        )
            .subscribe(value => {
                this.filtroBusqueda = value;
                this.tabla.filters(this.filtroBusqueda);
            });

        this.FilterEventSub$ = this._searchService.getFilterEvent().subscribe(event => {
            this.onFilterMenu(this._searchService.getFilterOrigin())
        });
    }

    onFilterMenu(origin: MatButton) {
        this._menuService
            .open(FilterMenuComponent, origin._elementRef, {
                filter: this.filter,
                data: this.tabla.getElementosExistentes()
            })
            .afterClosed()
            .subscribe({
                next: filter => {
                    if (filter) {
                        this.filter = filter;
                        this.extraParams = this.filter.deposito != -1 ? "filter[iddeposito]=" + this.filter.deposito + '&' : '';
                        this.extraParams += this.filter.usuario != -1 ? "filter[idusuario]=" + this.filter.usuario + '&' : '';
                        this.extraParams += this.filter.filtroFechaInicio != null ? "filter[fecha][gte]=" + this.filter.filtroFechaInicio + '&' : '';
                        this.extraParams += this.filter.filtroFechaFin != null ? "filter[fecha][lte]=" + this.filter.filtroFechaFin + '&' : '';

                        this.tabla.filters(this.filtroBusqueda);
                        // this._searchService.getSearchInput().setValue(this._tabsServices.getFiltroBuscadorPedido());

                    }
                },
            });
    }

    /**
 * Esta es la funcion que se encarga de la columna acciones dentro de la grilla dinamica
 * @param event 
 */
    generarAcciones(event: { name: string, params: any }) {
        switch (event.name) {
            case 'ver':
                this.verMovimientoConversion(event);
                break;
            case 'eliminar':
                this.deleteMovimientoConversion(event);
                break;
        }
    }

    /**
     * Esta funcion guarda un item
     */
    saveMovimientoConversion() {
        this.dialogo
            .open(SaveComponent, { panelClass: 'ayuda-dialog' })
            .componentInstance.creadoExitosamente
            .subscribe({
                next: () => {
                    this.alert.success("Movimiento conversion creado con éxito.");
                    this.tabla.filters(this.filtroBusqueda);
                },
            });
    }

    /**
     * Esta funcion modifica un item
     * @param event 
     */
    verMovimientoConversion(event) {
        this.dialogo
            .open(SaveComponent, { data: event.params.data, panelClass: 'my-dialog', autoFocus: false })
            .afterClosed()
            .subscribe({
                next: () => {
                    this.tabla.filters(this.filtroBusqueda);
                },
            });
    }

    /**
     * Esta funcion elimina un item
     * @param event 
     */
    deleteMovimientoConversion(event) {
        this.confirm.open({
            title: "Eliminar Movimiento conversion: " + event.params.data.fecha,
            message: `¿Desea eliminar el movimiento conversion realizado el: ${event.params.data.fecha} ${event.params.data.hora}; del depósito: ${event.params.data.deposito} ?`,
            dismissible: false,
            actions: {
                confirm: {
                    show: true,
                    color: 'warn',
                    label: 'Si',
                },
                cancel: {
                    show: true,
                    label: 'No',
                },
            },
        })
            .afterClosed()
            .subscribe(res => {
                if (res === 'confirmed') {
                    this._movimientoConversionService.deleteMovimientoConversion(event.params.data.idmovimientoconversion).subscribe({
                        next: (res) => {
                            // Manejar la respuesta exitosa
                            this.alert.success("Movimiento conversion eliminado con éxito.");
                            if (res) {
                                this.tabla.renderTabla()
                            }
                        },
                        error: (error) => {
                            // Manejar el error
                            console.error('Error en el servicio:', error);
                            this.alert.warning("Este movimiento conversion no puede eliminarse. Tiene elementos asociados.");
                        }
                    });
                }
            });
    }

    ngOnDestroy(): void {
        this._searchService.unsubscribe()
    }
}