import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'environments/environment';
import { Observable, Subject } from 'rxjs';
import { IMovimientoConversion } from '../models/movimiento-conversion';
import { AuthService } from '@core/auth/auth.service';
import { Response } from '@shared/models/response-with-token';

@Injectable()
export class MovimientoConversionService {
    constructor(private http: HttpClient, private _authService: AuthService) { }
    /**
     * Funcion en el Servicio 
     * @param movimiento_conversion 
     * @returns Crea un item nuevo
     */
    saveMovimientoConversion(movimiento_conversion: IMovimientoConversion): Observable<IMovimientoConversion> {
        return this.http.post<IMovimientoConversion>(`${environment.stock.movimiento_conversion}`, movimiento_conversion);
    }

    /**
     * Funcion en el servicio
     * @param idmovimientoconversion 
     * @returns Elimina un item
     */
    deleteMovimientoConversion(idmovimientoconversion: number): Observable<IMovimientoConversion> {
        return this.http.delete<IMovimientoConversion>(`${environment.stock.movimiento_conversion}` + '/' + idmovimientoconversion);
    }

    getDepositos(option?) {
        let f = '?';
        f += !option ? 'filter[activo]=1' : '';
        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.stock.depositos}${f}`)
            .subscribe((resp: any) => {
                // this._authService.accessToken = resp.token;
                subject.next({
                    list: resp.result
                })
            });
        return subject.asObservable()
    }

    getArticulosDepositosStock(deposito_origen: number, idarticulo?: number, idunidadmedida?: number) {
        let f = `?filter[deposito]=${deposito_origen}`;
        f += idarticulo ? `&filter[idarticulo]=${idarticulo}` : '';
        f += idunidadmedida ? `&filter[idunidadmedida]=${idunidadmedida}` : '';

        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.stock.view_stock_articulo_depositos}${f}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token;
                subject.next({
                    list: resp.result
                })
            });
        return subject.asObservable()
    }

    getUnidadesDeMedidaArticulos(idarticulo) {
        let f = `?filter[idarticulo]=${idarticulo}`
        f += `&expand=um`
        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.stock.articulo_unidad_medidas}${f}`)
            .subscribe((resp: any) => {
                // this._authService.accessToken = resp.token;
                subject.next({
                    list: resp.result
                })
            });
        return subject.asObservable()
    }

    getUsuarios() {
        let f = `?sort=asc`;
        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.login.view_usuarios}${f}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token;
                subject.next({
                    list: resp.result
                })
            });
        return subject.asObservable();
    }
}